import React, { useState, useEffect } from 'react'
import personService from './personService'
import ReactDOM from 'react-dom'
import './index.css'

const App = () => {
	const [persons, setPersons] = useState([])
	const [newName, setNewName] = useState('')
	const [newNumber, setNewNumber] = useState('')
	const [filter, setFilter] = useState('')
	const [displayNames, setDisplayNames] = useState(persons)
	const [notificationMessage, setNotificationMessage] = useState(null)

	useEffect(() => {
		personService.getAll().then((res) => {
			if (persons.length !== res.data.length) {
				setPersons(res.data)
			}
		})
		setDisplayNames(
			persons.filter((person) => {
				return person.name.toLowerCase().includes(filter)
			})
		)
	}, [filter, persons])

	const handleNameChange = (event) => {
		setNewName(event.target.value)
	}

	const handleNumberChange = (event) => {
		setNewNumber(event.target.value)
	}

	const handleFilterChange = (event) => {
		setFilter(event.target.value)
	}

	const submit = (event) => {
		event.preventDefault()

		// Checks if person with number already exists
		let found = ''
		for (let i in persons) {
			if (persons[i].number === newNumber) {
				found = newNumber
				break
			}
		}

		// Checks for an update
		let updatePerson = false
		for (let i in persons) {
			if (persons[i].name === newName) {
				updatePerson = persons[i]
				break
			}
		}

		if (found !== '') {
			alert('Number has already been saved!')
		} else if (newName === '' || newNumber === '') {
			alert('Name and Number cant be left empty')
		} else if (updatePerson) {
			const update = window.confirm(
				`${newName} is already added to the phonebook. Do you want to replace the old number with the new one?`
			)
			if (update) {
				const newPerson = { ...updatePerson, number: newNumber }
				personService.update(updatePerson.id, newPerson).then((newObject) => {
					setPersons(persons.map((person) => (person.id !== newPerson.id ? person : newPerson)))
					setDisplayNames(persons.map((person) => (person.id !== newPerson.id ? person : newPerson)))
					setNewNumber('')
					setNewName('')
				})
			}
		} else {
			// Person not found, creates new person
			const newPerson = { name: newName, number: newNumber }

			personService.create(newPerson).then((newObject) => {
				setPersons(persons.concat(newObject.data))
				setDisplayNames(persons.concat(newObject.data))
				setNewName('')
				setNewNumber('')
				setNotificationMessage({ content: `Added ${newObject.data.name}`, type: 'success' })
			})
		}
	}

	const deleteContact = (id) => {
		const delPerson = persons.find((person) => person.id === id)
		const del = window.confirm(`Are you sure you want to delete ${delPerson.name}?`)
		if (del) {
			personService
				.deleteContact(delPerson)
				.then((res) => {
					setPersons(persons.filter((person) => person.id !== delPerson.id))
					setDisplayNames(persons.filter((person) => person.id !== delPerson.id))
				})
				.catch((error) => {
					setNotificationMessage({
						content: `Information of ${delPerson.name} has already been removed from the server`,
						type: 'error'
					})
					console.log(error)
					personService.getAll().then((res) => {
						setPersons(res.data)
					})
				})
		}
	}

	return (
		<div>
			<h2>Phonebook</h2>
			<Notification message={notificationMessage} />
			<Filter filter={filter} handleFilterChange={handleFilterChange} />
			<PersonForm
				newName={newName}
				newNumber={newNumber}
				handleNumberChange={handleNumberChange}
				handleNameChange={handleNameChange}
				submit={submit}
			/>
			<h2>Numbers</h2>
			<Persons displayNames={displayNames} deleteContact={deleteContact} />
		</div>
	)
}

const Filter = ({ filter, handleFilterChange }) => {
	return (
		<p>
			Filter shown with <input value={filter} onChange={handleFilterChange} />
		</p>
	)
}

const PersonForm = ({ newName, newNumber, handleNameChange, handleNumberChange, submit }) => {
	return (
		<form onSubmit={submit}>
			<div>
				name: <input value={newName} onChange={handleNameChange} />
			</div>
			<div>
				number: <input value={newNumber} onChange={handleNumberChange} />
			</div>
			<div>
				<button type='submit'>Add</button>
			</div>
		</form>
	)
}

const Persons = ({ displayNames, deleteContact }) => {
	return (
		<div>
			{displayNames.map((person, i) => (
				<p key={i}>
					{person.name} {person.number} <button onClick={() => deleteContact(person.id)}>delete</button>
				</p>
			))}
		</div>
	)
}

const Notification = ({ message }) => {
	if (message === null) {
		return null
	}

	return <div className={message.type}>{message.content}</div>
}

ReactDOM.render(<App />, document.getElementById('root'))
